.advantages__item {
  text-align: left;
  padding-left: 47px;
  position: relative;

  &--residence {
    &::before {
      content: "";
      display: block;
      width: 39px;
      height: 37px;
      position: absolute;
      top: -7px;
      left: 0;
      background-image: url("../img/advantages/house.svg");
      background-position: center;
      background-repeat: no-repeat;

      @media (min-width: @tablet-width) {
        width: 75px;
        height: 72px;
        top: 60px;
        left: auto;
        background-size: 75px 72px;
      }

      @media (min-width: @desktop-width) {
        height: 73px;
        background-size: 75px 73px;
      }
    }

    @media (min-width: @tablet-width) {
      padding: 158px 31px 60px 32px;
    }

    @media (min-width: @desktop-width) {
      padding: 158px 60px 81px 58px;
    }
  }

  &--food {
    &::before {
      content: "";
      display: block;
      width: 40px;
      height: 35px;
      position: absolute;
      top: -6px;
      left: 0;
      background-image: url("../img/advantages/burger.svg");
      background-position: center;
      background-repeat: no-repeat;

      @media (min-width: @tablet-width) {
        width: 76px;
        height: 68px;
        top: 63px;
        left: auto;
        background-size: 76px 68px;
      }

      @media (min-width: @desktop-width) {
        width: 77px;
        height: 68px;
        background-size: 77px 69px;
      }
    }

    @media (min-width: @tablet-width) {
      padding: 158px 31px 60px 32px;
    }

    @media (min-width: @desktop-width) {
      padding: 158px 59px 81px;
    }
  }

  &--souvenirs {
    &::before {
      content: "";
      display: block;
      width: 32px;
      height: 38px;
      position: absolute;
      top: -8px;
      left: 5px;
      background-image: url("../img/advantages/gift.svg");
      background-position: center;
      background-repeat: no-repeat;

      @media (min-width: @tablet-width) {
        width: 64px;
        height: 75px;
        top: 57px;
        left: auto;
        right: auto;
        background-size: 64px 75px;
      }

      @media (min-width: @desktop-width) {
        width: 63px;
        background-size: 63px 75px;
      }
    }

    @media (min-width: @tablet-width) {
      padding: 137px 32px 60px 35px;
    }

    @media (min-width: @desktop-width) {
      padding: 158px 94px 81px 91px;
    }
  }

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  @media (min-width: @desktop-width) {
    max-width: 400px;
  }
}

.advantages__title {
  font-size: 18px;
  margin: 0;
  margin-bottom: 16px;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    margin-bottom: 23px;
  }
}

.advantages__item--food {
  .advantages__title {
    margin-left: 6px;

    @media (min-width: @tablet-width) {
      margin-left: 0;
    }
  }

  .advantages__description {
    margin-left: 5px;
    @media (min-width: @tablet-width) {
      margin-left: 0;
    }
  }
}

.advantages__item--souvenirs {
  .advantages__title,
  .advantages__description {
    margin-left: -2px;

    @media (min-width: @tablet-width) {
      margin-left: 0;
    }
  }
}

.advantages__description {
  margin: 0;
  color: rgba(51, 51, 51, 1);
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  @media (min-width: @tablet-width) {
    max-width: 193px;
  }

  @media (min-width: @desktop-width) {
    max-width: 282px;
  }
}

.advantages__break {
  @media (max-width: @mobile-width-only) {
    display: none;
  }
}

.advantages__item--souvenirs .advantages__break {
  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: block;
  }
}
